<template>
  <div class="login">
    <div class="left">LOGO</div>
    <div class="right">
      <div class="rightCenter">
        <h2>Connexion</h2>
        <label for="email">Adresse e-mail</label>
        <input type="text" v-model="mail" placeholder="" />
        <label for="password">Mot de passe</label>
        <input type="password" v-model="password" placeholder="" />
        <div class="error"></div>
        <button @click="login">Se connecter</button>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      mail: "",
      password: "",
    };
  },
  methods: {
    login() {
      axios
        .post("http://localhost:3000/api/auth/login", {
          mail: this.mail,
          password: this.password,
        })
        .then((response) => {
          if (response.status === 200) {
            localStorage.setItem("token", JSON.stringify(response.data.token));
            this.$router.push("/home");
          }
        })
        .catch((error) => {
          console.log(error.response.data);
          document.querySelector(".error").innerHTML =
            "Erreur: " + error.response.data.error;
        });
    },
  },
  mounted() {
    document.title = "Login";
    if (localStorage.getItem("token")) {
      this.$router.push("/home");
    }
  },
};
</script>
<style lang="scss" scoped>
body {
  font-size: 18px;
}
.login {
  display: flex;
  flex-direction: row;
}
.left {
  width: 100%;
  height: 100vh;
  background-color: #fd2d01;
}
.right {
  width: 100%;
  height: 100vh;
  display: flex;
  background-color: white;
}
.rightCenter {
  display: flex;
  flex-direction: column;
  width: 500px;
  margin-left: auto;
  margin-right: auto;
  align-self: center;
  input {
    width: auto;
    height: 45px;
    border-radius: 10px;
    border: none;
    padding-left: 10px;
    padding-right: 10px;
    border: 1px solid #bfbfbf;
    font-size: 18px;
  }
  input:focus {
    outline: none;
    border: 1px solid #4e4e4e;
  }
  button {
    height: 49px;
    width: auto;
    border: none;
    margin-top: 20px;
    background-color: #4e5166;
    color: white;
    border-radius: 10px;
    font-size: 18px;
  }
  button:hover {
    background-color: #3c3e4e;
    cursor: pointer;
  }
  label {
    margin-top: 15px;
    margin-bottom: 5px;
  }
}
h2 {
  font-weight: 100;
}
.error {
  color: red;
  margin-top: 15px;
  align-self: center;
}
</style>