<template>
  <div class="header">
    <div class="headerGroup">
      <router-link class="logo" :to="{ path: '/' }"></router-link>
    </div>
    <div class="headerGroup">
      <router-link class="homeIcon" :to="{ path: '/home' }"
        >Accueil</router-link
      >
      <!-- <router-link class="likeIcon" :to="{ path: '/liked' }"
        >Mes favoris</router-link
      > -->
    </div>
    <div class="headerGroup">
      <div class="userIcon">{{ user.username.charAt(0) }}</div>
      <div class="userName">{{ user.username }}</div>
      <router-link class="logoutIcon" :to="{ path: '/logout' }"></router-link>
    </div>
  </div>
</template>

<script>
import jwt_decode from "jwt-decode";
export default {
  name: "HeaderComponent",
  methods: {
    getUserInfos: function () {
      var decoded = jwt_decode(
        localStorage.getItem("token").replaceAll('"', "")
      );
      this.user = decoded;
    },
  },
  beforeMount() {
    this.getUserInfos();
  },
  data() {
    return {
      user: [],
      userFirstLetter: "",
    };
  },
};
</script>

<style lang="scss" scoped>
body {
  margin: 0;
}
.headerGroup:nth-child(1) {
  justify-content: left !important;
}
.headerGroup:nth-child(3) {
  justify-content: right !important;
}
.header {
  width: auto;
  height: 55px;
  background-color: #4e5166;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;

  .headerGroup {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;

    > div {
      margin-left: 5px;
      margin-right: 5px;
    }

    .logo {
      width: 30px;
      height: 30px;
      background-image: url("@/assets/logo.svg");
      background-size: contain;
      background-repeat: no-repeat;
    }
    .userIcon {
      width: 35px;
      height: 35px;
      background-color: #ffd7d7;
      border-radius: 50%;
      background-size: cover;
      font-size: 18px;
      display: flex;
      place-items: center;
      justify-content: center;
      color: #fd2d01;
      text-transform: uppercase;
    }
    .likeIcon {
      padding: 10px;
      background-size: cover;
      font-size: 18px;
      display: flex;
      place-items: center;
      justify-content: center;
      color: white;
      text-decoration: none;
    }
    .likeIcon:hover {
      background-color: #2c2e3a;
    }

    .homeIcon {
      padding: 10px;
      background-size: cover;
      font-size: 18px;
      display: flex;
      place-items: center;
      justify-content: center;
      color: white;
      text-decoration: none;
    }
    .homeIcon:hover {
      background-color: #2c2e3a;
    }

    .logoutIcon {
      margin-left: 30px;
      width: 20px;
      height: 20px;
      background-size: cover;
      font-size: 18px;
      display: flex;
      place-items: center;
      justify-content: center;
      color: #fd2d01;
      background-image: url("@/assets/logout.svg");
      filter: invert(100%) sepia(0%) saturate(0%) hue-rotate(37deg)
        brightness(104%) contrast(101%);
    }

    .userName {
      color: white;
      text-decoration: none;
    }
  }
}
</style>
