<template>
  <div>
    <HeaderComponent></HeaderComponent>
    <div class="container">
      <!-- for each posts -->
      <div class="post" v-for="post in posts" :key="post.id">
        <PostComponent :data="post"></PostComponent>
      </div>
      <!-- for each comments -->
      <PostCommentComponent></PostCommentComponent>
      <div class="comment" v-for="comment in comments" :key="comment.id">
        <CommentComponent :data="comment"></CommentComponent>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import HeaderComponent from "../components/Header.vue";
import PostComponent from "../components/Post.vue";
import CommentComponent from "../components/Comment.vue";
import PostCommentComponent from "../components/NewComment.vue";
export default {
  components: {
    HeaderComponent,
    PostComponent,
    CommentComponent,
    PostCommentComponent,
  },
  methods: {
    getAllPosts: function () {
      axios
        .get("http://localhost:3000/api/posts/" + this.id, {
          headers: {
            Authorization:
              "Bearer " + localStorage.getItem("token").replaceAll('"', ""),
          },
        })
        .then((response) => {
          if (response.status === 200) {
            this.posts[0] = response.data;
          } else {
            console.log("error");
          }
        })
        .catch((error) => {
          console.log(error.response.data);
        });
    },
    getAllComments: function () {
      axios
        .get("http://localhost:3000/api/posts/" + this.id + "/comments", {
          headers: {
            Authorization:
              "Bearer " + localStorage.getItem("token").replaceAll('"', ""),
          },
        })
        .then((response) => {
          if (response.status === 200) {
            this.comments = response.data;
          } else {
            console.log("error");
          }
        })
        .catch((error) => {
          console.log(error.response.data);
        });
    },
  },
  data() {
    return {
      id: this.$route.params.id,
      posts: [],
      comments: [],
    };
  },
  beforeMount() {
    this.getAllPosts();
    this.getAllComments();
  },
};
</script>

