<template>
  <div class="newP">
    <div class="newPost">
      <div class="userIcon">{{ user.username.charAt(0) }}</div>
      <input
        class="inputContent"
        type="text"
        v-model="content"
        placeholder="Quoi de neuf?"
      />
      <div class="addImage"></div>
      <button class="buttonPublish" @click="post">Publier</button>
    </div>
  </div>
</template>

<script>
import jwt_decode from "jwt-decode";
import axios from "axios";
export default {
  name: "NewPost",
  data() {
    return {
      content: "",
      user: [],
    };
  },
  methods: {
    getUserInfos: function () {
      var decoded = jwt_decode(
        localStorage.getItem("token").replaceAll('"', "")
      );
      this.user = decoded;
    },
    post() {
      axios
        .post(
          "http://localhost:3000/api/posts/",
          {
            content: this.content,
          },
          {
            headers: {
              Authorization:
                "Bearer " + localStorage.getItem("token").replaceAll('"', ""),
            },
          }
        )
        .then((response) => {
          if (response.status === 201) {
            this.$router.push("/post/" + response.data.id);
          }
        })
        .catch((error) => {
          console.log(error.response.data);
          document
            .querySelector(".inputContent")
            .setAttribute("style", "border: 1px solid red");
        });
    },
  },
  beforeMount() {
    this.getUserInfos();
  },
};
</script>

<style lang="scss" scoped>
.newPost {
  background-color: white;
  display: flex;
  flex-direction: row;
  padding: 20px;
  justify-content: space-between;
  border-bottom: 1px solid #e9e9e9;
  gap: 10px;

  .inputContent {
    background-color: #f3f3f3;
    border-radius: 20px;
    border: none;
    padding-left: 20px;
    padding-right: 20px;
    font-size: 16px;
    flex: auto;
  }
  .inputContent:focus {
    border: 1px solid gray;
    outline: none;
  }

  .buttonPublish {
    height: 50px;
    width: 105px;
    font-size: 16px;
    border: none;
    background-color: #4e5166;
    color: white;
    border-radius: 10px;
  }
  .buttonPublish:hover {
    opacity: 0.9;
    cursor: pointer;
  }

  .addImage {
    width: 30px;
    height: 30px;
    background-image: url("@/assets/addimg.svg");
    background-size: contain;
    background-repeat: no-repeat;
    align-self: center;
    filter: invert(71%) sepia(0%) saturate(1507%) hue-rotate(254deg)
      brightness(110%) contrast(95%);
  }
}
</style>
