<template>
  <div class="post">
    <div class="postHeader">
      <div class="userIcon">{{ data.username.charAt(0) }}</div>
      <div class="postInfos">
        <div class="postUsername">{{ data.username }}</div>
        <router-link class="postDate linkZ" :to="{ path: '/post/' + data.id }">
          {{ newTime }}
          <div v-if="data.modified === 1">(Modifié)</div>
        </router-link>
      </div>
    </div>
    <div class="postContent">{{ data.content }}</div>
    <div class="postStats">
      <router-link
        class="postStatsLeft linkZ"
        :to="{ path: '/post/' + data.id }"
      >
        <div class="postComIcon"></div>
        {{ data.comments }}
      </router-link>
      <div class="postStatsRight">
        <div
          @click="like('like', data.id)"
          v-if="liked === 0"
          class="postLikeIcon"
        ></div>
        <div
          @click="like('dislike', data.id)"
          v-else
          class="postLikeIcon postLikeIconLiked"
        ></div>
        <div v-if="data.liked === 0" class="countLike">
          {{ data.likes }}
        </div>
        <div v-else class="countLike likedRed">
          {{ data.likes }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import moment from "moment";
import "moment/locale/fr"; // without this line it didn't work
moment.locale("fr");
export default {
  name: "PostComponent",
  props: {
    data: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      newTime: "",
      liked: "",
    };
  },
  mounted() {
    this.newTime = moment(
      moment(this.data.date * 1000).format("YYYYMMDDkkmmss"),
      "YYYYMMDDkkmmss"
    ).fromNow();
    this.liked = this.data.liked;
  },
  methods: {
    like(action, id) {
      axios
        .post(
          "http://localhost:3000/api/posts/" + id + "/" + action,
          {
            content: "",
          },
          {
            headers: {
              Authorization:
                "Bearer " + localStorage.getItem("token").replaceAll('"', ""),
            },
          }
        )
        .then((response) => {
          if (response.status === 200) {
            if (response.data.message === "1") {
              this.$el
                .querySelector(".postLikeIcon")
                .classList.add("postLikeIconLiked");
              this.$el.querySelector(".countLike").classList.add("likedRed");
              let count = parseInt(
                this.$el.querySelector(".countLike").innerText
              );
              count = count + 1;
              this.$el.querySelector(".countLike").classList.add("likedRed");
              this.$el.querySelector(".countLike").innerText = count;
              this.liked = 1;
            } else if (response.data.message === "0") {
              this.$el
                .querySelector(".postLikeIcon")
                .classList.remove("postLikeIconLiked");
              this.$el.querySelector(".countLike").classList.remove("likedRed");
              let count = parseInt(
                this.$el.querySelector(".countLike").innerText
              );
              count = count - 1;
              this.$el.querySelector(".countLike").classList.remove("likedRed");
              this.$el.querySelector(".countLike").innerText = count;
              this.liked = 0;
            } else {
              console.log(response.data.message);
            }
          } else {
            console.log("error");
          }
        })
        .catch((error) => {
          console.log(error.response.data);
        });
    },
  },
};
</script>

<style lang="scss" scoped>
.post {
  background-color: white;
  padding: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid #e9e9e9;
}

.postHeader {
  display: flex;
  flex-direction: row;
  gap: 10px;
  margin-bottom: 20px;
}

.postInfos {
  align-self: center;
}

.postDate {
  color: #bcbcbc;
  display: flex;
  flex-direction: row;
  div {
    margin-left: 5px;
  }
}
.postContent {
  color: #4a4a4a;
  margin-bottom: 30px;
  word-wrap: break-word;
}
.postStats {
  display: flex;
  flex-direction: row;
  place-content: space-evenly;
  color: #bcbcbc;
}
.postStatsRight {
  display: flex;
  flex-direction: row;
  gap: 5px;
  align-items: center;
}
.postLikeIcon {
  width: 25px;
  height: 25px;
  background-image: url(@/assets/heart-regular.svg);
  filter: invert(79%) sepia(10%) saturate(4%) hue-rotate(338deg) brightness(94%)
    contrast(92%);
  background-repeat: no-repeat;
}
.postLikeIcon:hover {
  cursor: pointer;
}
.postLikeIconLiked {
  background-image: url(@/assets/heart-solid.svg);
  filter: invert(32%) sepia(85%) saturate(5192%) hue-rotate(0deg)
    brightness(98%) contrast(108%);
  animation: like 0.7s forwards;
}
@keyframes like {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.likedRed {
  color: #fd2d01;
}
.postStatsLeft {
  display: flex;
  flex-direction: row;
  gap: 5px;
  align-items: center;
}
.postComIcon {
  width: 25px;
  height: 25px;
  background-image: url(@/assets/com.svg);
  filter: invert(79%) sepia(10%) saturate(4%) hue-rotate(338deg) brightness(94%)
    contrast(92%);
  background-repeat: no-repeat;
}
.postComIcon:hover {
  cursor: pointer;
}
</style>
