<template>
  <div class="postComment">
    <div class="left">
      <div class="userIcon">{{ user.username.charAt(0) }}</div>
    </div>
    <input
      class="middle"
      placeholder="Ecrire un commentaire ici..."
      type="text"
    />
    <button class="right">Commenter</button>
  </div>
</template>
<script>
import jwt_decode from "jwt-decode";
export default {
  methods: {
    getUserInfos: function () {
      var decoded = jwt_decode(
        localStorage.getItem("token").replaceAll('"', "")
      );
      this.user = decoded;
    },
  },
  data() {
    return {
      user: [],
    };
  },
  beforeMount() {
    this.getUserInfos();
  },
};
</script>
<style lang="scss" scoped>
.postComment {
  padding: 20px;
  display: flex;
  place-content: space-between;
  gap: 10px;
}
.middle {
  width: 100%;
  background-color: #f3f3f3;
  border-radius: 20px;
  border: none;
  padding-left: 20px;
  padding-right: 20px;
  font-size: 16px;
  flex: auto;
}
.right {
  height: 50px;
  font-size: 16px;
  border: none;
  background-color: #4e5166;
  color: white;
  border-radius: 10px;
  padding-left: 15px;
  padding-right: 15px;
}
</style>